<template>
	<div class="movie-item" :class="{'active': isActive}" v-link="{ name: 'movie', params: { id: item.MovieID }}" @mouseover.stop="becomeActive" @mouseleave.stop="becomeInactive">
		<div class="movie-cover">
			<div class="movie-overlay" v-show="isActive"></div>
			<img v-show="isActive" class="play-icon" src="../assets/play.svg">
			<img :class="{'blurred': isActive}" :src="'/c/' + item.MovieID">
		</div>
		<div class="movie-description">
			<!-- <img class="img-responsive" src="../assets/title.svg"> -->
			<h3 :title="item.Name">{{item.Name}}</h3>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		item: {
			type: Object,
		}
	},
	data: function() {
		return {
			isActive: false
		}
	},
	methods: {
		becomeActive: function() {
			this.isActive = true;
		},
		becomeInactive: function() {
			this.isActive = false;
		}
	}
}
</script>

<style lang="less">

.movie-item {
	margin-top: 8px;
	margin-bottom: 8px;
	width: 49%;
	background: #fff;
	transition: all .2s linear;
	&.active {
		-webkit-box-shadow: 0 15px 30px rgba(0,0,0,0.1);
	    box-shadow: 0 15px 30px rgba(0,0,0,0.1);
	    -webkit-transform: translate3d(0, -2px, 0);
	    transform: translate3d(0, -2px, 0);
	}
	.movie-cover {
		position: relative;
		width: 100%;
		cursor: pointer;
		.movie-overlay {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: #000;
			opacity: .5;
			z-index: 2;
			transition: all .2s linear;
		}
		.play-icon {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 3;
			transform: translate(-50%, -50%);
		}
		img {
			transition: all .2s linear;
			max-width: 100%;
			height: auto;
			margin: 0 auto;
		}
	}
	.movie-description {
		padding-left: 8px;
		padding-right: 8px;
		h3 {
			margin-top: 5px;
		    margin-bottom: 5px;
		    font-size: 1.5rem;
		    height: 26px;
		    line-height: 26px;
		    overflow: hidden;
	        text-overflow: ellipsis;
	        text-align: center;
	        font-weight: 100;
	        cursor: pointer;
	        /*white-space: nowrap;*/
		}
	}
	
}
@media screen and (min-width:1500px) {
	.movie-item {
	    width: 33%;
	}
}
</style>


