<template>
    <div id="app">
        <sidebar></sidebar>
        <div class="contents">
            <router-view
                class="view">
            </router-view>
        </div>
    </div>
</template>

<style lang="less">
html {
    font-size: 62.5%;
}
body {
    margin: 0;
    font-family: MicrosoftYaHei, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", '', Arial, sans-serif;
    font-weight: 400;
}

.img-responsive {
    display: block;
    max-width: 100%;
    height: auto;
}
.sidebar,
.contents {
    float: left;
}
.contents {
    position: relative;
    left: 250px;
    width: calc(~"100% - 250px");
    background-color: #f5f5f5;
}
</style>

<script type="text/javascript">
import Sidebar from './components/Sidebar'

export default {
    components: {
        Sidebar,
    },
    events: {
        'changeCategory': function(id) {
            var vm = this;
            vm.$broadcast('changeCategory', id);
        }
    }
}
</script>




