<template>
	<div class="page">
		<ul>
			<li :class="{'active': currentPage == 1}" @click.stop="viewPage(1)">1</li>
			
			<li v-if="currentPage - 2 > 2">...</li>
			<li v-for="page in pages" :class="{'active': currentPage == page}" @click.stop="viewPage(page)">{{page}}</li>
			<li v-if="currentPage + 2 < length">...</li>
			
			<li :class="{'active': currentPage == length}" @click.stop="viewPage(length)">{{length}}</li>

		</ul>
	</div>
</template>

<script type="text/javascript">
import $ from 'jquery';

export default {
    props: {
        length: {
            type: Number
        }
    },
	data: function() {
		return {
			id: -1,
			pages: [],
			currentPage: 0,
		}
	},
    ready: function() {
        const vm = this;
        vm.currentPage = 1;
    },
	methods: {
		viewPage: function(page) {
            const vm = this;
			// $(this).addClass('active');
            vm.currentPage = page;
			this.$dispatch('viewPage', page);
		},
		
	},
	watch: {
		'currentPage': function(newVal) {
			const vm = this;
            let length = vm.length;
            /*
            1:  2, 3, 4, 5
            2:  2, 3, 4, 5
            3:  2, 3, 4, 5
            4:  2, 3, 4, 5, 6
            5:  3, 4, 5, 6, 7
            6:  4, 5, 6, 7, 8
            ...
            32: 30, 31, 32, 33, 34
            33: 31, 32, 33, 34, 35
            34: 32, 33, 34, 35, 36
            35: 33, 34, 35, 36
            36: 33, 34, 35, 36
            37: 33, 34, 35, 36
            */
            if (newVal <= 3) {
                vm.pages = [2, 3, 4, 5];
            } else if(newVal + 2 < length) {
                // newVal >= 4 && newVal < vm.length - 2
                vm.pages = [newVal - 2, newVal -1 , newVal, newVal + 1, newVal + 2];
            } else {
                vm.pages = [length - 4, length - 3, length - 2, length - 1];
            }
		}
	}
}
</script>

<style lang="less">
@blue: #5677fc;
.page {
	width: 100%;
	height: 30px;
	margin-top: 5px;
	margin-bottom: 10px;
	ul {
		display: flex;
		margin: 0 auto;
		padding: 0;
		li {
			list-style: none;
		    background: #fff;
		    cursor: pointer;
		    font-weight: 100;
		    font-size: .8rem;
		    padding: 6px 10px;
		    text-align: center;
		    &.active {
		    	background: @blue;
		    	color: #fff;
		    }
		}
	}
}
</style>


