<template>
    <div class="player">
        <video class="video-js vjs-default-skin vjs-big-play-centered" controls preload="auto" data-setup='{}' :src="url">
            <source :src="url" type="video/mp4">
            <source src="http://vjs.zencdn.net/v/oceans.webm" type="video/webm">
            <source src="http://vjs.zencdn.net/v/oceans.ogv" type="video/ogg">
        </video>
    </div>
    
</template>

<style lang="less" scoped>
.player {
    width: 100%;
    .video-js {
        width: 100%;
        padding-bottom: 56.2%;

    }

    
}

</style>

<script type="text/javascript">
import $ from 'jquery'
import NProgress from 'nprogress'
import '../plugins/nprogress.css'
var videojs = require('video.js');
require('../plugins/video-js.css');

export default {
    data: function() {
        return {
            url: "http://love.xxtvpic.com/PIC/201609/0917/IPZ780/IPZ780.mp4",
        }
    },
    route: {

        data ({ to }) {
            var router = this;
            const movieId = to.params.id;
            
            NProgress.start();
            $.get(`http://loli.vc/info/${movieId}`, function(data) {
                let poster = data.Message.CoverImg;
                let url = data.Message.Img.replace("jpg", "mp4");
                router.url = url;
                // router.poster = poster;

                router.$nextTick(function() {
                    // player.src(url);
                    let video = router.$el.querySelector('.video-js');
                    var player = videojs(video, {}, function() {});
                    console.log(`player`);
                    player.poster(poster);
                    NProgress.done();
                });
                
            });
        },

    },
}

</script>


