<template>
	<div class="tag" :class="{'active': isActive}" @click.stop="changeCategory(tag.ID)">
		{{tag.Name}}
	</div>
</template>

<style lang="less" scoped>
@white: #fff;
@teal: #009688;
@blue: #5677fc;

@colorA: #6a3093;
@colorB: #a044ff;
.tag {
	display: inline-block;
    padding: 4px 10px;
    margin-right: 8px;
    margin-bottom: 13px;
	border-left: 1px solid @colorA;
	border-right: 1px solid @colorB;
	box-sizing: border-box;
	background-position: 0 0, 0 100%;
	background-repeat: no-repeat;
	background-size: 100% 1px;
	background-image: linear-gradient(to left, @colorA 0%, @colorB 100%), linear-gradient(to left, @colorA 0%, @colorB 100%);
    border-radius: 3px;
    font-size: .8rem;
	color: @teal;
	transition: border .4s ease;
	&.active {
		background: linear-gradient(to left, @colorA, @colorB);
		color: @white;
	}
    &:hover {
    	background: linear-gradient(to left, @colorA, @colorB);
		color: @white;
    }
    
}
</style>

<script>
import $ from 'jquery'

export default {
	data: function() {
		return {
			isActive: false,
		}
	},
	props: {
		tag: {
			type: Object,
		}
	},
	methods: {
		changeCategory: function(id) {
			let vm = this;
			$('.tag.active').removeClass('active');
			vm.isActive = true;
			vm.$dispatch('changeCategory', id);
		}
	}
}
</script>


