<template>
	<div class="category">
		<div class="title">
			<img class="img-responsive" src="../assets/category.svg">
			<span></span>
		</div>
		<tag v-for="tag in tags" :tag="tag"></tag>
		<div class="buttons">
			<div class="previous" >
				<img src="../assets/previous.svg" @click.stop="previousPage">
			</div>
			<div class="next">
				<img src="../assets/next.svg" @click.stop="nextPage">
			</div>
		</div>
	</div>
</template>

<style lang="less" scoped>
@teal: #009688;
@grey: #9e9e9e;
@black: #212121;
@blue: #5677fc;

.category {
	.title {
		margin-top: 20px;
		margin-bottom: 15px;
		font-size: 1.5rem;
		color: @black;
		img {
			display: inline-block;
			width: 24px;
			margin-right: 8px;
			vertical-align: middle;
		}
		
	}
	.buttons {
		display: flex;
		justify-content: space-between;
		.next {
			img {
				width: 79%;
				margin-top: 3px;
				cursor: pointer;
			}
		}
	}
}	
</style>


<script type="text/javascript">
import Tag from './Tag.vue'
import NProgress from 'nprogress'
import '../plugins/nprogress.css'
import $ from 'jquery'

export default {
	data: function() {
		return {
			page: 1,
			size: 24,
			totalPage: 0,
			tags: [] 
		}
	},
	components: {
		Tag,
	},
	ready: function() {
		var vm = this;
		NProgress.start();
		$.get(`http://loli.vc/categories?page=${vm.page}&size=${vm.size}`, function(data) {
			// console.log(data.Message.Movies.slice(0));
			vm.tags = data.Message.Data;
			vm.totalPage = data.Message.PageCount;
			NProgress.done();
		});
	},
	methods: {
		previousPage: function() {
			const vm = this;
			vm.page = vm.page - 1 > 0 ? vm.page - 1: vm.page;
		},
		nextPage: function() {
			const vm = this;
			vm.page = vm.page + 1 > vm.totalPage ? vm.page : vm.page + 1;
		},
	},
	
	watch: {
		'page': function(newVal, oldVal) {
			const vm = this;
			NProgress.start();
			if (newVal !== oldVal) {
				$.get(`http://loli.vc/categories?page=${newVal}&size=${vm.size}`, function(data) {
					// console.log(data.Message.Movies.slice(0));
					vm.tags = data.Message.Data;
					NProgress.done();
				});
			}
			
		}
	}

}
</script>


