<template>
	<div class="movie-lists">
		<movie-item :item="item" v-for="item in movies"></movie-item>
		<!-- <movie-item v-link="{ path: '/movie/' + item.MovieID }" :item="item" v-for="item in movies"></movie-item> -->
		<page :length="length"></page>
	</div>
</template>

<script type="text/javascript">

import $ from 'jquery';
import MovieItem from './MovieItem.vue';
import Page from './Page.vue';
import NProgress from 'nprogress';
import '../plugins/nprogress.css'
// import './less/util.less';

export default {
	data: function() {
		return {
			page: 1,
			perPage: 12,
			length: 0,
			movies: [],
			id: -1,
		}
	},
	ready: function() {
		let vm = this;
		vm.getPage(1);
	},
	events: {
		'viewPage': function(page) {
			this.page = page;
			this.getPage(page);
			console.log(`currentPage is ${page}`);
		},
		'changeCategory': function(id) {
			this.id = id;
			this.getPage(this.page);
		}	
	},
	components: {
		MovieItem,
		Page,
	},
	methods: {
		getPage: function(page) {
			let vm = this;
			vm.page = page;
			NProgress.start();
			$.get(`http://loli.vc/category/${vm.id}?page=${page}`, function(data) {
				// console.log(data.Message.Movies.slice(0));
				vm.movies = data.Message.Movies.slice(0);
				vm.length = data.Message.PageCount;
				NProgress.done();
			});
			
		},
		
	},
}
</script>

<style lang="less">
.movie-lists {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	padding: 0 10px;
	
	
}
</style>


